body {
  background-color:    rgb(101, 101, 206)
}
.App{
    background-color: rgb(75, 49, 223);
    min-height: 500px;
    width: 400px;
    margin: 40px auto;
}
.todo-form input
{
    background-color: rgb(154, 140, 236);
    border: 0;
    width: 240px;
    height: 50px;
    padding: 0px 20px;
    margin: 20px;
    font-size: 18px;
    border-radius: 10px;
    color: #FFF;
}
.todo-form input::placeholder{
    color: rgb(243, 226, 226);
}
.todo-form input:focus,.todo-form button:focus{
    outline: none;
}
.todo-form button
{
    height: 50px;
    width: 60px;
    border-radius: 5px;
    border: 0;
    background-color:rgb(202, 156, 24) ;
    cursor: pointer;
    font-weight: bolder;
    color: #FFF;
}