.list{
    color:#fff;
    width: 300px;
    background-color: #6575D0;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}
.list p{
    padding: 10px;
    position: relative;
}
.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}
.faIcons{
    margin: 0px 5px;
    font-size: 15px;
}
.list p input{
    background-color:#6575D0;
    border: 0px;
    color: #FFF;
    font-size: 18px;
}
.list p input:focus{
    outline: none;
}